					<textarea v-else class="v textarea" v-model="form.temperament" placeholder="‰æãÂ¶ÇÔºöÊÖµÊáíÔºå‰∏çÁà±Âä®ÔºåËøòÊúâÁÇπÂ∞èÂÇ≤Â®á" />
