							:key="`photo-${i}-${photoUpdateTrigger}`" class="g-wrapper">
							<image class="g" :src="getPhotoSrc(g)" mode="aspectFill" @tap="preview(i)" />
							<view v-if="editMode" class="g-delete" @tap="deletePhoto(i)">√ó</view>
