<script setup>
	import {
		ref,
		computed,
		reactive,
		watch,
		nextTick
	} from 'vue'
	import {
		onLoad
	} from '@dcloudio/uni-app'
	import {
		api
	} from '@/utils/api.js'
	import {
		uploadImage,
		compressImage
	} from '@/utils/upload.js'

	// Â§¥ÂÉè‰∏ãËΩΩÁºìÂ≠òÔºåÈÅøÂÖçÈáçÂ§ç‰∏ãËΩΩ
	const avatarCache = new Map()

	// ÁÖßÁâáÁºìÂ≠ò
	const photoCache = new Map()
	const photoUpdateTrigger = ref(0)

	const pet = ref({})

	// ËßÑËåÉÂåñÂêéÁ´ØËøîÂõûÁöÑÁñ´ËãóÊï∞ÊçÆ‰∏∫Êï∞ÁªÑ
	function normalizeVaccines(val) {
		if (Array.isArray(val)) {
			return val
		}
		if (typeof val === 'string') {
			try {
				const parsed = JSON.parse(val)
				if (Array.isArray(parsed)) {
					return parsed
				}
			} catch (e) {
				// ÂèØËÉΩÊòØÈÄóÂè∑ÂàÜÈöîÁöÑÂ≠óÁ¨¶‰∏≤
			}
			return val
				.split(',')
				.map(s => s.trim())
				.filter(Boolean)
		}
		return []
	}

	// Ëé∑ÂèñÂÆ†Áâ©Â§¥ÂÉèÁöÑÂèØÊòæÁ§∫ srcÔºà‰∏é user.vue ‰øùÊåÅ‰∏ÄËá¥Ôºâ
	function getPetAvatarSrc(url) {
		if (!url) return '/static/logo.png'

		// Áªü‰∏ÄËßÑËåÉÂåñÔºö
		// 1) /uploads/ Áõ∏ÂØπË∑ØÂæÑ ‚Üí ÊãºÊé•ÈùôÊÄÅÂüüÂêç
		// 2) Âº∫Âà∂ http ‚Üí httpsÔºåÂéªÊéâ :80
		let normalized = url
		if (normalized.startsWith('/uploads/')) {
			normalized = `https://pet-api.zbinli.cn${normalized}`
		}
		if (normalized.startsWith('http://pet-api.zbinli.cn')) {
			normalized = normalized.replace('http://pet-api.zbinli.cn', 'https://pet-api.zbinli.cn')
		}
		normalized = normalized.replace('://pet-api.zbinli.cn:80', '://pet-api.zbinli.cn')

		// Êú¨Âú∞ÊàñÈùôÊÄÅË∑ØÂæÑÁõ¥Êé•ËøîÂõû
		if (normalized.startsWith('wxfile://') || normalized.startsWith('/static/')) {
			return normalized
		}

		// ÂëΩ‰∏≠ÁºìÂ≠ò
		if (avatarCache.has(normalized)) {
			return avatarCache.get(normalized)
		}

		// ‰∏ãËΩΩÁΩëÁªúÂõæÁâáÂà∞Êú¨Âú∞‰∏¥Êó∂Êñá‰ª∂
		uni.downloadFile({
			url: normalized,
			success: (res) => {
				if (res.statusCode === 200 && res.tempFilePath) {
					avatarCache.set(normalized, res.tempFilePath)
					pet.value = {
						...(pet.value || {})
					}
				} else {
					avatarCache.set(normalized, '/static/logo.png')
					pet.value = {
						...(pet.value || {})
					}
				}
			},
			fail: () => {
				avatarCache.set(normalized, '/static/logo.png')
				pet.value = {
					...(pet.value || {})
				}
			}
		})

		// ‰∏ãËΩΩ‰∏≠ËøîÂõûÂç†‰Ωç
		return '/static/logo.png'
	}

	// Ëé∑ÂèñÁÖßÁâáÁöÑÂèØÊòæÁ§∫ src
	function getPhotoSrc(url) {
		if (!url) return '/static/index/add.png'

		// Áªü‰∏ÄËßÑËåÉÂåñÔºö
		// 1) /uploads/ Áõ∏ÂØπË∑ØÂæÑ ‚Üí ÊãºÊé•ÈùôÊÄÅÂüüÂêç
		// 2) Âº∫Âà∂ http ‚Üí httpsÔºåÂéªÊéâ :80
		let normalized = url
		if (normalized.startsWith('/uploads/')) {
			normalized = `https://pet-api.zbinli.cn${normalized}`
		}
		if (normalized.startsWith('http://pet-api.zbinli.cn')) {
			normalized = normalized.replace('http://pet-api.zbinli.cn', 'https://pet-api.zbinli.cn')
		}
		normalized = normalized.replace('://pet-api.zbinli.cn:80', '://pet-api.zbinli.cn')

		// Êú¨Âú∞ÊàñÈùôÊÄÅË∑ØÂæÑÁõ¥Êé•ËøîÂõû
		if (normalized.startsWith('wxfile://') || normalized.startsWith('/static/')) {
			return normalized
		}

		// ÂëΩ‰∏≠ÁºìÂ≠ò
		if (photoCache.has(normalized)) {
			return photoCache.get(normalized)
		}

		// ‰∏ãËΩΩÁΩëÁªúÂõæÁâáÂà∞Êú¨Âú∞‰∏¥Êó∂Êñá‰ª∂
		uni.downloadFile({
			url: normalized,
			success: (res) => {
				if (res.statusCode === 200 && res.tempFilePath) {
					photoCache.set(normalized, res.tempFilePath)
					photoUpdateTrigger.value++
				} else {
					console.warn('ÁÖßÁâá‰∏ãËΩΩÂ§±Ë¥•:', normalized, res.statusCode)
					photoCache.set(normalized, '/static/index/add.png')
					photoUpdateTrigger.value++
				}
			},
			fail: (err) => {
				console.error('ÁÖßÁâá‰∏ãËΩΩÂ§±Ë¥•:', normalized, err)
				photoCache.set(normalized, '/static/index/add.png')
				photoUpdateTrigger.value++
			}
		})

		return '/static/index/add.png'
	}

	onLoad(async (query) => {
				// ËÆæÁΩÆÂØºËà™Ê†èËÉåÊôØËâ≤‰∏éÈ°µÈù¢ËÉåÊôØÈ°∂ÈÉ®È¢úËâ≤‰∏ÄËá¥
				uni.setNavigationBarColor({
					frontColor: '#000000',
					backgroundColor: '#fff1a8'
				})

		if (query?.pet) {
			try {
				const data = JSON.parse(decodeURIComponent(query.pet))
				Object.assign(pet.value, data)
				// ÂêåÊ≠•ÊòæÁ§∫Áî®ÁöÑÁñ´Ëãó‰∏éÊÄßÊ†ºÁ≠âÂ≠óÊÆµ
				vaccines.value = normalizeVaccines(data.vaccines)
				temperament.value = data.temperament || ''
					// Â¶ÇÊûúË∑ØÁî±ÈáåÂ∏¶Êù•ÁöÑÊï∞ÊçÆÈáåÁñ´Ëãó‰∏∫Á©∫ÔºåÂ∞ùËØïÂÜç‰ªéÂêéÁ´ØÊãâ‰∏ÄÊ¨°ÊúÄÊñ∞Êï∞ÊçÆ
					if (!data.vaccines || (Array.isArray(data.vaccines) && data.vaccines.length === 0)) {
					try {
							const fresh = await api.getPet(pet.value.id)
						vaccines.value = normalizeVaccines(fresh && fresh.vaccines)
						temperament.value = (fresh && fresh.temperament) || temperament.value
					} catch (refreshErr) {
							// ignore
					}
				}
					nextTick(() => {})
				console.log('ËµãÂÄºÂêéÁöÑpet.value:', pet.value);

					// ÊµãËØïÈÄªËæëÁßªÈô§
			} catch (e) {
					// ignore
			}
		}
		// ÊãâÂèñËØ•ÂÆ†Áâ©ÁöÑÂ™í‰ΩìÂõæÁâá
		if (pet.value?.id) {
			try {
				const res = await api.getMedia({
					petId: pet.value.id
				})
				const mediaList = Array.isArray(res) ? res : (res.media || res.data || [])

				// ÊåâÁÖßÂàõÂª∫Êó∂Èó¥ÊéíÂ∫èÔºàÊúÄÊó©Âà∞ÊúÄÊôöÔºâ
				const sortedMediaList = mediaList.sort((a, b) => {
					const timeA = new Date(a.createdAt || a.created_at || 0).getTime()
					const timeB = new Date(b.createdAt || b.created_at || 0).getTime()
					return timeA - timeB // ÂçáÂ∫èÔºöÊúÄÊó©ÁöÑÂú®ÂâçÈù¢
				})

					gallery.value = sortedMediaList.map(m => m.url).filter(Boolean)
			} catch (err) {
					// ignore
			}
		}
	})

	const recent = ref([{
			id: 'r1',
			time: '‰ªäÂ§© 08:00',
			type: 'ÂñÇÈ£ü',
			desc: 'Áå´Á≤Æ 60g'
		},
		{
			id: 'r2',
			time: 'Êò®Â§© 21:10',
			type: 'Ê∏ÖÊ¥Å',
			desc: 'Èì≤Á†Ç'
		}
	])

	const vaccines = ref([])
	const temperament = ref('')
	const gallery = ref([])

	// ÁºñËæëÊ®°Âºè
	const editMode = ref(false)
	const genders = ref(['Â•≥Áîü', 'Áî∑Áîü'])
	const genderIndex = ref(1)
	const vaccineOptions = ref([
		// Áå´Á±ªÂ∏∏ËßÅÁñ´Ëãó
		'Â∑≤Êé•ÁßçÁå´‰∏âËÅîÁñ´Ëãó',
		'Â∑≤Êé•ÁßçÁå´‰∏âËÅîÁ¨¨‰∫åÈíà/Âä†Âº∫',
		'Â∑≤Êé•ÁßçÁå´ÁôΩË°ÄÁóÖÁñ´Ëãó(FeLV)',
		'Â∑≤Êé•ÁßçÁãÇÁä¨Áñ´Ëãó',
		// Áä¨Á±ªÂ∏∏ËßÅÁñ´Ëãó
		'Â∑≤Êé•ÁßçÁä¨‰∫îËÅîÁñ´Ëãó',
		'Â∑≤Êé•ÁßçÁä¨ÂÖ≠ËÅîÁñ´Ëãó',
		'Â∑≤Êé•ÁßçÁä¨‰∏ÉËÅîÁñ´Ëãó',
		'Â∑≤Êé•ÁßçÂ∞èÁä¨ÁªÜÂ∞èÁñ´Ëãó',
		'Â∑≤Êé•ÁßçÁä¨ÁòüÁÉ≠Áñ´Ëãó',
		'Â∑≤Êé•ÁßçÂçöÂæ∑ÁâπÊ∞èÊîØÊ∞îÁÆ°ÁÇéÁñ´Ëãó',
		'Â∑≤Êé•ÁßçÈí©Á´ØËû∫Êóã‰ΩìÁñ´Ëãó',
		// ÂÖ∂‰ªñ
		'Â∑≤Êé•ÁßçÂÖ∂‰ªñÁñ´Ëãó'
	])
	const form = reactive({
		name: '',
		months: '',
		weight: '',
		gender: 'male',
		breed: '',
		color: '',
		neutered: false,
		birthday: '',
		startTogether: '',
		avatar: '',
		vaccines: [],
		temperament: '',
		gallery: []
	})
	const originalGallery = ref([]) // ‰øùÂ≠òÁºñËæëÂâçÁöÑÂéüÂßãÁÖßÁâá

	function startEdit() {
		editMode.value = true
		Object.assign(form, {
			...pet.value,
			vaccines: [...vaccines.value],
			temperament: temperament.value,
			avatarUrl: pet.value.avatarUrl,
			gallery: [...gallery.value]
		})
		originalGallery.value = [...gallery.value] // ‰øùÂ≠òÂéüÂßãÁÖßÁâá
		genderIndex.value = form.gender === 'male' ? 1 : 0
	}

	function cancelEdit() {
		editMode.value = false
		gallery.value = [...originalGallery.value] // ÊÅ¢Â§çÂéüÂßãÁÖßÁâá
	}
	async function saveEdit() {
		try {
			uni.showLoading({
				title: '‰øùÂ≠ò‰∏≠...'
			})

			// ÂáÜÂ§áÊõ¥Êñ∞Êï∞ÊçÆ
			const updateData = {
				name: form.name,
				months: form.months,
				weight: form.weight,
				gender: form.gender,
				breed: form.breed,
				color: form.color,
				neutered: form.neutered,
				birthday: form.birthday,
				startTogether: form.startTogether,
				temperament: form.temperament,
				vaccines: form.vaccines
			}

			// Â¶ÇÊûúÊúâÊñ∞Â§¥ÂÉèÔºåÈúÄË¶ÅÂÖà‰∏ä‰º†
			if (form.avatar && form.avatar.startsWith('wxfile://')) {
				try {
					const {
						uploadImage,
						compressImage
					} = await import('@/utils/upload.js')
					const compressedPath = await compressImage(form.avatar, 0.8)
					const avatarUrl = await uploadImage(compressedPath, 'avatar')
					updateData.avatarUrl = avatarUrl
				} catch (error) {
					console.warn('Â§¥ÂÉè‰∏ä‰º†Â§±Ë¥•:', error)
					uni.showToast({
						title: 'Â§¥ÂÉè‰∏ä‰º†Â§±Ë¥•ÔºåÂÖ∂‰ªñ‰ø°ÊÅØÂ∑≤‰øùÂ≠ò',
						icon: 'none'
					})
				}
			} else if (form.avatarUrl) {
				updateData.avatarUrl = form.avatarUrl
			}

			// Êõ¥Êñ∞ÂÆ†Áâ©Âü∫Êú¨‰ø°ÊÅØ
			await api.updatePet(pet.value.id, updateData)

			// Â§ÑÁêÜÁÖßÁâáÊõ¥Êñ∞
			console.log('üîç Ê£ÄÊü•ÁÖßÁâáÊõ¥Êñ∞...')
			console.log('form.gallery:', form.gallery)
			console.log('gallery.value:', gallery.value)

			if (form.gallery && form.gallery.length > 0) {
				// Ê£ÄÊü•ÊòØÂê¶ÊúâÊñ∞Ê∑ªÂä†ÁöÑÁÖßÁâáÔºàÊú¨Âú∞Ë∑ØÂæÑÔºâ
				const newPhotos = form.gallery.filter(photo => photo.startsWith('wxfile://'))
				console.log('Êñ∞ÁÖßÁâáÊï∞Èáè:', newPhotos.length)
				console.log('Êñ∞ÁÖßÁâáË∑ØÂæÑ:', newPhotos)

				if (newPhotos.length > 0) {
					try {
						console.log('ÂºÄÂßã‰∏ä‰º†ÁÖßÁâá...')

						// ÂéãÁº©Âπ∂‰∏ä‰º†Êñ∞ÁÖßÁâá
						const uploadPromises = newPhotos.map(async (photoPath) => {
							console.log('ÂéãÁº©ÁÖßÁâá:', photoPath)
							const compressedPath = await compressImage(photoPath, 0.7)
							console.log('ÂéãÁº©ÂêéË∑ØÂæÑ:', compressedPath)
							const uploadedUrl = await uploadImage(compressedPath, 'gallery')
							console.log('‰∏ä‰º†ÊàêÂäüÔºåURL:', uploadedUrl)
							return uploadedUrl
						})

						const uploadedUrls = await Promise.all(uploadPromises)
						console.log('ÊâÄÊúâÁÖßÁâá‰∏ä‰º†ÂÆåÊàê:', uploadedUrls)

						// ÂàõÂª∫Â™í‰ΩìËÆ∞ÂΩï
						console.log('ÂàõÂª∫Â™í‰ΩìËÆ∞ÂΩï...')
						console.log('petId:', pet.value.id)
						console.log('urls:', uploadedUrls)

						const mediaResult = await api.createMedia({
							petId: pet.value.id,
							type: 'image',
							urls: uploadedUrls,
							description: 'ÂÆ†Áâ©ÁÖßÁâá'
						})

						console.log('Â™í‰ΩìËÆ∞ÂΩïÂàõÂª∫ÁªìÊûú:', mediaResult)
						console.log('ÊàêÂäü‰∏ä‰º†ÁÖßÁâá:', uploadedUrls.length, 'Âº†')

						// ÈáçÊñ∞‰ªéÊúçÂä°Âô®Ëé∑ÂèñÊéíÂ∫èÂêéÁöÑÁÖßÁâáÂàóË°®
						try {
							const res = await api.getMedia({
								petId: pet.value.id
							})
							const mediaList = Array.isArray(res) ? res : (res.media || res.data || [])

							// ÊåâÁÖßÂàõÂª∫Êó∂Èó¥ÊéíÂ∫èÔºàÊúÄÊó©Âà∞ÊúÄÊôöÔºâ
							const sortedMediaList = mediaList.sort((a, b) => {
								const timeA = new Date(a.createdAt || a.created_at || 0).getTime()
								const timeB = new Date(b.createdAt || b.created_at || 0).getTime()
								return timeA - timeB // ÂçáÂ∫èÔºöÊúÄÊó©ÁöÑÂú®ÂâçÈù¢
							})

							gallery.value = sortedMediaList.map(m => m.url).filter(Boolean)
							console.log('‰øùÂ≠òÂêéÈáçÊñ∞Âä†ËΩΩÁÖßÁâáÔºåÊåâÊó∂Èó¥ÊéíÂ∫è:', sortedMediaList.map(m => ({
								url: m.url,
								createdAt: m.createdAt || m.created_at
							})))
						} catch (err) {
							console.warn('ÈáçÊñ∞Âä†ËΩΩÁÖßÁâáÂ§±Ë¥•Ôºå‰ΩøÁî®Êú¨Âú∞Êõ¥Êñ∞:', err)
							// Â¶ÇÊûúÈáçÊñ∞Âä†ËΩΩÂ§±Ë¥•Ôºå‰ΩøÁî®Êú¨Âú∞Êõ¥Êñ∞
							const existingPhotos = form.gallery.filter(photo => !photo.startsWith('wxfile://'))
							gallery.value = [...existingPhotos, ...uploadedUrls]
						}

					} catch (error) {
						console.error('ÁÖßÁâá‰∏ä‰º†Â§±Ë¥•:', error)
						uni.showToast({
							title: 'ÁÖßÁâá‰∏ä‰º†Â§±Ë¥•ÔºåÂÖ∂‰ªñ‰ø°ÊÅØÂ∑≤‰øùÂ≠ò',
							icon: 'none'
						})
					}
				} else {
					console.log('Ê≤°ÊúâÊñ∞ÁÖßÁâáÈúÄË¶Å‰∏ä‰º†')
				}
			} else {
				console.log('Ê≤°ÊúâÁÖßÁâáÈúÄË¶ÅÂ§ÑÁêÜ')
			}

			// Êõ¥Êñ∞Êú¨Âú∞Êï∞ÊçÆ
			pet.value = {
				...pet.value,
				...updateData
			}
			vaccines.value = [...form.vaccines]
			temperament.value = form.temperament
			gallery.value = [...form.gallery]
			editMode.value = false

			uni.hideLoading()
			uni.showToast({
				title: '‰øùÂ≠òÊàêÂäü',
				icon: 'success'
			})

		} catch (error) {
			uni.hideLoading()
			console.error('‰øùÂ≠òÂ§±Ë¥•:', error)
			uni.showToast({
				title: '‰øùÂ≠òÂ§±Ë¥•',
				icon: 'none'
			})
		}
	}

	function onGenderChange(e) {
		genderIndex.value = Number(e.detail.value || 0);
		form.gender = genderIndex.value === 1 ? 'male' : 'female'
	}

	function onVaccinesChange(e) {
		form.vaccines = e.detail.value || []
	}

	function pickAvatar() {
		uni.chooseImage({
			count: 1,
			sizeType: ['compressed'],
			success: res => {
				form.avatar = res.tempFilePaths[0]
			}
		})
	}

	function pickGallery() {
		uni.chooseImage({
			count: 9,
			sizeType: ['compressed'],
			success: res => {
				form.gallery = form.gallery.concat(res.tempFilePaths)
			}
		})
	}

	function deletePhoto(index) {
		if (editMode.value) {
			uni.showModal({
				title: 'Á°ÆËÆ§Âà†Èô§',
				content: 'Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÂº†ÁÖßÁâáÂêóÔºü',
				confirmText: 'Âà†Èô§',
				cancelText: 'ÂèñÊ∂à',
				confirmColor: '#ff4757',
				success: (res) => {
					if (res.confirm) {
						form.gallery.splice(index, 1)
					}
				}
			})
		}
	}

	function preview(index) {
		const list = editMode.value ? form.gallery : gallery.value
		if (!Array.isArray(list) || list.length === 0) return
		uni.previewImage({
			current: index,
			urls: list
		})
	}

	async function deletePet() {
		uni.showModal({
			title: 'Á°ÆËÆ§Âà†Èô§',
			content: `Á°ÆÂÆöË¶ÅÂà†Èô§ÂÆ†Áâ©"${pet.value.name}"ÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§çÔºÅ`,
			confirmText: 'Âà†Èô§',
			cancelText: 'ÂèñÊ∂à',
			confirmColor: '#ff4757',
			success: async (res) => {
				if (!res.confirm) return
				try {
					await api.deletePet(pet.value.id)
					uni.showToast({
						title: 'ÂÆ†Áâ©Â∑≤Âà†Èô§',
						icon: 'success'
					})
					// ÈÄöÁü•ÂÖ∂‰ªñÈ°µÈù¢Âà∑Êñ∞
					uni.$emit && uni.$emit('pets:changed')
					setTimeout(() => {
						uni.navigateBack()
					}, 600)
				} catch (e) {
					uni.showToast({
						title: 'Âà†Èô§Â§±Ë¥•',
						icon: 'none'
					})
				}
			}
		})
	}

	const togetherDays = computed(() => {
		const start = new Date(pet.value.startTogether).getTime()
		const today = new Date().setHours(0, 0, 0, 0)
		return Math.max(1, Math.floor((today - start) / 86400000) + 1)
	})

	function goEdit() {
		uni.navigateTo({
			url: '/pages/editPet/editPet'
		})
	}

	function goAlbum() {
		uni.navigateTo({
			url: '/pages/album/album'
		})
	}

	// ÂõæÁâáÂä†ËΩΩ‰∫ã‰ª∂
	function onAvatarLoad(e) {}

	function onAvatarError(e) {
		try {
			e && e.target && (e.target.src = '/static/logo.png')
		} catch {}
	}
</script>

